@import 'vars';
@import 'mixins';
@import 'blocks/payment-block';

.page-title {
    margin: 35px 0 35px 0;
}

.products-container {
    .products-container__inner {
        background-color: #fff;
        padding: 45px 10px;
        @include clearfix;
        @include products-grid(12px, 30px);
        margin-left: 0 !important;
        margin-right: 0 !important;
    }
}

.custom-order {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &.content-block {
        padding: 50px 0px 50px 45px;
    }
    .custom-order__images-block {
        width: 525px;
        height: 500px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            position: absolute;
        }
        img.left-center {
            left: 0;
            margin: auto;
        }
        img.right-top {
            right: 0;
            top: 0;
        }
        img.center-bottom {
            margin: auto;
            bottom: 0;
        }
    }
    .custom-order__content-text {
        width: 535px;
        .custom-order__title {
            color: map_get($blue, default);
            font-family: $merri-font;
            font-size: 45px;
            font-weight: 700;
        }
        .custom-order__text {
            margin-top: 30px;
            color: map_get($gray, default);
            font-family: Roboto;
            font-size: 14px;
            font-weight: 400;
        }
        .look-detalies {
            margin-top: 30px;
        }
    }
}

.videos-slider-block { 
    .videos-slider-block__title {
        color: map_get($blue, default);
        font-family: $merri-font;
        font-size: 24px;
        font-weight: 700;
        line-height: 30px;
    }
    .videos-slider-block__description {
        margin-top: 15px;
        color: #a6a6a6;
        font-family: $merri-font;
        font-size: 14px;
        font-weight: 700;
    }
    .join-now {
        margin: 35px auto 0 auto;
        display: block;
    }
}

.videos-slider-wrapper {
    margin-top: 45px;
    position: relative;
    .swiper-slide {
        width: 246px !important;
    }
    .videos-slider__btn-next, 
    .videos-slider__btn-prev {
        top: 60px;
        margin-top: 0;
    }

    .videos-slider__btn-next {
        right: -30px;
    }

    .videos-slider__btn-prev {
        left: -30px;
    }
}

.video-preview {
    display: inline-block;
    text-align: left;
    .video-preview__link {
        width: 246px;
        display: block;
        .video-preview-inner {
            height: 138px;
            overflow: hidden;
            position: relative;
            .video-preview__img {
                max-width: 100%;
                 transition: transform .4s;
            }
            .video-preview__play-btn {
                width: 80px;
                height: 58px;
                position: absolute;
                top: 50%;
                margin-top: -30px;
                left: 50%;
                margin-left: -39px;
                background-image: url('../img/icon/play.png');
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
               
            }
            .video-preview__time {
                position: absolute;
                bottom: 5px;
                right: 5px;
                padding: 3px 5px;
                color: #fff;
                font-size: 13px;
                font-family: $roboto-font;
                background: rgba(0,0,0,.8);
                border-radius: 3px;
            }
        }
        .video-preview__description {
            color: map_get($blue, default);
            font-family: $roboto-font;
            font-size: 16px;
            font-weight: 700;
            margin-top: 15px;
            transition: color .3s;
        }
    }
    .video-preview__link:hover {
            .video-preview__img {
            transform: scale(1.05);
        }
        .video-preview__description {
            color: map_get($blue, default);
        }
    }
}

.event-agencies {
    .event-agencies__left-block,
    .event-agencies__right-block {
        width: 50%;
        position: relative;
    }
    .event-agencies__left-block {
        float: left;
        padding-right: 50px;
    }

    .event-agencies__right-block {
        float: right;
        padding-left: 50px;
    }

    .event-agencies__title {
        color: map_get($blue, default);
        font-family: $merri-font;
        font-size: 24px;
        width: 85%;
        font-weight: 700;
        margin-top: 35px;
    }

    .event-agencies__description {
        margin-top: 35px;
        font-family: $roboto-font;
        color: map_get($gray, default);
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
    }

    .how-it-work-btn {
        margin-top: 25px;
    }
}



.about-us {
    background: #ffffff;
    @include clearfix;
    position: relative;
    &__start {
        float: left;
        width: 55%;
        padding: 35px 45px;
        padding: 65px 60px 35px 40px;
    }
    &__end {
        float: left;
    }
    &__block {
        margin-bottom: 30px;
        &:first-child {
            margin-bottom: 40px;
        }
    }
    &__title {
        margin-bottom: 30px;
    }
    &__text {
        color: map_get($gray, default);
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
    }
    &__link {
        color: map_get($blue, default);
        text-decoration: underline;
        display: inline-block;
        font-size: 14px;
        font-weight: 400;
        transition: color .3s;
        &:hover {
            color: map_get($blue, hover);
            text-decoration: underline;
        }
        &:active {
            color: map_get($blue, active);
            text-decoration: underline;
        }
    }
    &__pic {
        width: 495px;
        height: 100%;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        right: 0;
    }
}

.subscribe {
    padding: 50px 50px 60px 80px;
    background-color: #ffffff;
    text-align: center;
    .block-title {
        margin-bottom: 65px;
    }
}

.contact-us {
    background-color: #ffffff;
    padding: 30px 50px 70px 50px;
    text-align: center;
    position: relative;
    .block-title--small {
        margin-bottom: 70px;
    }
   
    .blue-btn {
        margin-top: 35px;
        margin-bottom: 50px;
    }

    .email {
        padding-left: 0;
        &__link {
            font-size: 14px;
            color: map_get($gray, default);
        }
        &:hover a{
            color: map_get($gray, hover);
        }
        &:active a{
            color: map_get($gray, active);
        }

    }
}

